.dashboard {
  min-height: 100vh;
  background: var(--light-gray);
}

.dashboard-content {
  padding: 2rem;
  max-width: 1400px;
  margin: 0 auto;
}

.dashboard-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}

.dashboard-header h1 {
  color: var(--rbc-blue);
  font-size: 2.5rem;
}

.overview-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
  margin-bottom: 3rem;
}

.overview-card {
  background: white;
  padding: 1.5rem;
  border-radius: 15px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.overview-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.15);
}

.overview-card h3 {
  color: var(--dark-gray);
  font-size: 0.9rem;
  font-weight: 500;
  margin-bottom: 0.5rem;
  text-transform: uppercase;
}

.overview-card .value {
  font-size: 2rem;
  font-weight: bold;
  color: var(--rbc-blue);
}

.overview-card .change {
  display: inline-block;
  margin-top: 0.5rem;
  padding: 0.25rem 0.5rem;
  border-radius: 5px;
  font-size: 0.9rem;
  font-weight: 600;
}

.overview-card .change.positive {
  background: rgba(76, 175, 80, 0.1);
  color: var(--success);
}

.overview-card .change.negative {
  background: rgba(244, 67, 54, 0.1);
  color: var(--error);
}

.portfolio-chart-section {
  background: white;
  padding: 2rem;
  border-radius: 15px;
  margin-bottom: 3rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.portfolio-chart-section h2 {
  color: var(--rbc-blue);
  margin-bottom: 1.5rem;
}

.portfolios-section {
  margin-bottom: 3rem;
}

.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
}

.portfolio-actions {
  display: flex;
  gap: 0.75rem;
  align-items: center;
}

.refresh-portfolios-btn {
  background: white;
  color: var(--rbc-blue);
  border: 2px solid var(--rbc-blue);
  padding: 0.5rem 1rem;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 0.9rem;
}

.refresh-portfolios-btn:hover:not(:disabled) {
  background: var(--rbc-blue);
  color: white;
  transform: translateY(-2px);
}

.refresh-portfolios-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  transform: none;
}

.section-header h2 {
  color: var(--rbc-blue);
}

.add-portfolio-btn {
  background: var(--rbc-yellow);
  color: var(--rbc-blue);
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 10px;
  font-weight: 600;
  font-size: 1rem;
}

.portfolio-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  gap: 1.5rem;
}

.no-portfolios {
  background: white;
  padding: 3rem;
  border-radius: 15px;
  text-align: center;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.no-portfolios p {
  color: var(--dark-gray);
  margin-bottom: 1.5rem;
  font-size: 1.1rem;
}

.create-portfolio-btn {
  background: var(--rbc-blue);
  color: white;
  border: none;
  padding: 0.75rem 2rem;
  border-radius: 10px;
  font-weight: 600;
  font-size: 1rem;
}

.quick-actions {
  background: white;
  padding: 2rem;
  border-radius: 15px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.quick-actions h2 {
  color: var(--rbc-blue);
  margin-bottom: 1.5rem;
}

.action-buttons {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}

.action-btn {
  flex: 1;
  min-width: 150px;
  padding: 1rem;
  border: none;
  border-radius: 10px;
  font-weight: 600;
  font-size: 1rem;
  color: white;
}

.action-btn.deposit {
  background: var(--success);
}

.action-btn.simulate {
  background: var(--rbc-yellow);
  color: var(--rbc-blue);
}

.action-btn.manage {
  background: var(--rbc-blue);
}

.no-client-message {
  background: white;
  padding: 4rem;
  border-radius: 15px;
  text-align: center;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  margin-top: 4rem;
}

.no-client-message h2 {
  color: var(--rbc-blue);
  margin-bottom: 1rem;
}

.no-client-message p {
  color: var(--dark-gray);
  margin-bottom: 2rem;
  font-size: 1.1rem;
}

.create-client-btn {
  background: var(--rbc-blue);
  color: white;
  border: none;
  padding: 1rem 2rem;
  border-radius: 10px;
  font-weight: 600;
  font-size: 1.1rem;
}

/* Quick Create Client Styles */
.create-client-options {
  display: flex;
  gap: 1rem;
  justify-content: center;
  margin-top: 1rem;
}

.create-client-btn.primary {
  background: var(--rbc-blue);
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.create-client-btn.primary:hover {
  background: var(--rbc-blue-dark);
  transform: translateY(-2px);
}

.create-client-btn.secondary {
  background: white;
  color: var(--rbc-blue);
  border: 2px solid var(--rbc-blue);
  padding: 0.75rem 1.5rem;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.create-client-btn.secondary:hover {
  background: var(--rbc-blue);
  color: white;
  transform: translateY(-2px);
}

.quick-create-form {
  background: white;
  padding: 1.5rem;
  border-radius: 12px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  margin-top: 1rem;
  max-width: 400px;
  margin-left: auto;
  margin-right: auto;
}

.quick-create-form .form-group {
  margin-bottom: 1rem;
}

.quick-create-form input {
  width: 100%;
  padding: 0.75rem;
  border: 2px solid #e0e0e0;
  border-radius: 8px;
  font-size: 1rem;
  transition: border-color 0.3s ease;
}

.quick-create-form input:focus {
  outline: none;
  border-color: var(--rbc-blue);
}

.quick-create-form input:disabled {
  background-color: #f5f5f5;
  cursor: not-allowed;
}

.quick-create-form small {
  color: var(--medium-gray);
  font-size: 0.85rem;
  margin-top: 0.25rem;
  display: block;
}

.form-actions {
  display: flex;
  gap: 0.75rem;
  justify-content: center;
  margin-top: 1.5rem;
}

.create-btn {
  background: var(--rbc-blue);
  color: white;
  border: none;
  padding: 0.75rem 1.25rem;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.create-btn:hover:not(:disabled) {
  background: var(--rbc-blue-dark);
  transform: translateY(-2px);
}

.create-btn:disabled {
  background: #ccc;
  cursor: not-allowed;
  transform: none;
}

.cancel-btn {
  background: white;
  color: var(--medium-gray);
  border: 2px solid #e0e0e0;
  padding: 0.75rem 1.25rem;
  border-radius: 8px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.cancel-btn:hover:not(:disabled) {
  border-color: var(--medium-gray);
  color: var(--dark-gray);
}

.cancel-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.client-debug {
  background: #f8f9fa;
  padding: 1rem;
  border-radius: 8px;
  margin: 1rem 0;
  border-left: 4px solid var(--rbc-blue);
}

.refresh-btn {
  background: var(--rbc-blue);
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 6px;
  cursor: pointer;
  font-size: 0.9rem;
  margin-top: 0.5rem;
}

.refresh-btn:hover {
  background: var(--rbc-blue-dark);
}

/* Token refresh button */
.refresh-token-btn {
  background: var(--rbc-gold);
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 6px;
  cursor: pointer;
  font-size: 0.9rem;
  margin-right: 0.5rem;
  transition: all 0.2s ease;
}

.refresh-token-btn:hover {
  background: #d4a017;
  transform: translateY(-1px);
}

.portfolio-actions {
  display: flex;
  gap: 0.5rem;
  align-items: center;
}